.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.app-old-portfolio{
  font-size: min(100px, max(6px, 1vmin)) !important; 
  text-align: center !important;
  font-weight: 800 !important;
  padding: 0px !important;
  height: 3.5em;
  width: 6.5em;
  
}



.app-technologies{
  font-size: min(100px, max(6px, 1vmin)) !important;
  text-align: center !important;
  font-weight: 800 !important;
  padding: 0px !important;
  height: 3.5em;
  width: 8em;
  overflow: auto;
  
}

.footer-buttons-holder{
  display: grid;
  grid-template-columns: 1fr 14fr;
  width: 100%;
  z-index: 100;
  right: -42%;
  margin-top: 5%;
  position: absolute;
}


.fade-in{
   animation-name: fade-in;
   animation-duration: 1s;
}

.fade-out{
    animation-name: fade-out;
   animation-duration: 1s;
}

.tech-z-order{
  z-index: 200;
}

@keyframes fade-in{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

@keyframes fade-out{
  from{
    opacity: 1;
  }
  to{
    opacity: 0;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

