.container-projects{
overflow: auto;    
height: 50vh;
} 

.project-item{
    
    height: min(600px, max(45px, 5em));
    font-size: min(100px, max(6px, 2vmin));
    position: relative;
    vertical-align: text-top;
}



.project-item button {
    position: absolute;
    font-size: min(100px, max(6px, 1.5vmin));
    text-align: center;
    top: 1vh;
    width: 6em;
    height:min(250px, max(30px, 4em));
    width:min(1000px, max(80px, 6em));
    float: left;
}

.project-item .cloud-project{
    right: 0.5vw;
    bottom: 0;
    top: auto;
    height:min(100px, max(10px, 3.5em));
} 

.project-item .machine-project{
    right: 0.5vw;
    top: 0;
    height:min(100px, max(10px, 3.5em));
}


.toogle-text-project, .card-text-project{ 
    text-align: center;
    font-size: min(max(6x, 1vmin), 100px);
    font-weight: 800;
    text-align: center;
    display: flex;
    margin: auto;
    align-items: center;
    white-space: pre-line;
    
}

.cloud-works-project{
    font-size: min(100px, max(8px, 1vmin));
    font-weight: 800;
    position: absolute;
    right: 1.5vw;
    bottom: 0;
    height: min(250px, max(15px, 3em));
}

.machine-works-project{
    font-size: min(100px, max(8px, 1vmin));
    font-weight: 800;
    position: absolute;
    right: 1.5vw;
    height: min(250px, max(15px, 3em));
}


